@charset "UTF-8";
.reason__title:after, .introducing__title:after, .cuisines__title::after {
  content: "";
  width: 4rem;
  height: .05rem;
  background: #f57365;
  display: block;
  margin-top: 10px;
}

.owl-reviews {
  height: 100%;
  position: relative;
}

.owl-reviews::before, .owl-reviews::after {
  position: absolute;
  content: '';
  pointer-events: none;
}

.owl-reviews::before {
  background: rgba(0, 0, 0, 0.25);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.owl, .owl-theme, .owl-stage-outer, .owl-stage, .owl-item, .owl-item > * {
  height: 100%;
}

.owl-nav {
  position: absolute;
  display: flex;
  top: 0;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  width: 100%;
  pointer-events: none;
}

.owl-prev, .owl-next {
  font-size: 3em !important;
  position: relative;
  color: #FFF !important;
  z-index: 1;
  opacity: 0.85;
  transition: opacity 0.5s ease;
}

.owl-prev:hover, .owl-next:hover {
  opacity: 1;
  transition: opacity 0.5s ease;
}

.owl-prev {
  left: 2rem;
}

.owl-next {
  right: 2rem;
}

.owl-dot {
  margin: 0 .1em .25em;
}

.owl-dot span {
  width: 10px;
  height: 10px;
  margin: 5px 7px;
  background: #D6D6D6;
  display: block;
  -webkit-backface-visibility: visible;
  transition: opacity .2s ease;
  border-radius: 50%;
}

.owl-dot.active span {
  background: #a3a3a3;
}

.owl-dots {
  position: absolute;
  bottom: 0;
  margin: 0 auto;
  right: 0;
  left: 0;
  text-align: center;
  bottom: -1rem;
  z-index: 3;
}

.owl-prev, .owl-next, .owl-dot {
  border: none;
  padding: 0;
  background: transparent;
  color: #D6D6D6;
  pointer-events: all;
  cursor: pointer;
}

.owl-prev span, .owl-next span, .owl-dot span {
  margin: 0 !important;
}

body {
  font-family: 'Lato', sans-serif;
  font-size: 14px;
}

.btn {
  padding: 10px 30px;
  border: 2px solid #FFF;
  color: #FFF;
  display: inline-block;
}

.main-banner {
  background: url(../img/main-image.jpg) no-repeat 0 0/cover;
  padding: 150px 0;
}

@media only screen and (max-width: em(768px)) {
  .main-banner {
    padding: 50px 0;
  }
}

.reason__icon {
  border-radius: 50%;
  border: 2px solid;
  width: 100px;
  height: 100px;
}

.reason__title:after {
  margin-left: auto;
  margin-right: auto;
}

.introducing__title {
  letter-spacing: 0.5px;
}

.reviews {
  background: rgba(74, 57, 73, 0.85) url(../img/review-background.png) no-repeat center center/cover;
  padding: 75px 0;
}

.review__pic {
  width: 80px;
  height: 80px;
  background: white;
  border-radius: 50%;
  margin: 0 auto 15px auto;
}

.review__cite {
  line-height: 1.5rem;
}

.review__author {
  margin-top: 1rem;
}

.review__author:before {
  content: "—";
  margin-right: .5em;
}

.cuisines__title::after {
  margin-left: auto;
  margin-right: auto;
}

.cuisine__title {
  letter-spacing: 1px;
}

.cuisine__layer {
  background: rgba(0, 0, 0, 0.35);
}
